function toggleSearch() {
  let button = $('.header__trigger');
  let search = $('.header__search');
  let back = $('.header__back');
  let dropdown = $('.search-dropdown');
  let dropdownWrapper = $('.search-dropdown__wrapper');
  button.on('click', function() {
    search.addClass('active');
    dropdown.addClass('active');
  });
  back.on('click', function() {
    search.removeClass('active');
    dropdown.removeClass('active');
  });
  $('.header__search input').on('focus', function() {
    search.addClass('active');
    dropdown.addClass('active');
  });
  $(document).mouseup(function(e) {
    if (!dropdownWrapper.is(e.target) && !search.is(e.target) && search.has(e.target).length === 0 && dropdownWrapper.has(e.target).length === 0) {
      dropdown.removeClass('active');
      search.removeClass('active');
    }
  });
  
}
toggleSearch();