function toggleProductInfo() {
  let button = $('.product-info__button-pc');
  let container = $('.product-info__container');
  for (let i = 0; i < Array.from(button).length; i++) {
    $(button[i]).on('click', function () {
      let id = button[i].getAttribute('data-id');
      let block = $('.product-info__container[data-id="' + id + '"]');
      button.removeClass('active');
      $(this).addClass('active');
      container.removeClass('active');
      $(block).attr('data-id', $(this).attr('data-id')).addClass('active');
    });
  }
}
toggleProductInfo();

