import Swiper from "swiper";
import { Navigation } from 'swiper/modules';

function sliderBrands() {
  new Swiper('.brands__slider .swiper', {
    modules: [ Navigation ],
    spaceBetween: 12,
    slidesPerView: 3.7,
    grabCursor: true,
    navigation: {
      prevEl: '.brands__slider .swiper-button-prev',
      nextEl: '.brands__slider .swiper-button-next',
    },
    breakpoints: {
      1280: {
        slidesPerView: 6,
        spaceBetween: 116,
      },
    },
  });
}
sliderBrands();