import Swiper from "swiper";
import { Grid, Navigation } from 'swiper/modules';

function sliderProducts() {
  // Находим все элементы слайдеров на странице
  const sliders = document.querySelectorAll('.products-slider__slider .swiper');

  // Инициализируем Swiper для каждого найденного элемента
  sliders.forEach((slider) => {
    new Swiper(slider, {
      modules: [Navigation, Grid],
      spaceBetween: 16,
      slidesPerView: 2,
      allowTouchMove: false,
      navigation: {
        prevEl: slider.closest('.products-slider__slider').querySelector('.swiper-button-prev'),
        nextEl: slider.closest('.products-slider__slider').querySelector('.swiper-button-next'),
      },
      grid: {
        rows: 2,
        fill: "row"
      },
      breakpoints: {
        1280: {
          slidesPerView: 4,
          grabCursor: true,
          allowTouchMove: true,
          spaceBetween: 40,
          grid: {
            rows: 1,
            fill: "row"
          },
        }
      }
    });
  });
}

sliderProducts();
