import Swiper from "swiper";
import { Navigation } from 'swiper/modules';

function sliderBlogMain() {
  new Swiper('.blog__main .swiper', {
    modules: [ Navigation ],
    spaceBetween: 12,
    slidesPerView: 1,
    grabCursor: true,
    breakpoints: {
      767: {
        slidesPerView: 1,
        spaceBetween: 24,
      },
      1280: {
        slidesPerView: 2,
        spaceBetween: 24,
      }
    },
    navigation: {
      prevEl: '.swiper-button-prev',
      nextEl: '.swiper-button-next',
    },
  });
}
sliderBlogMain();