import Swiper from "swiper";
import { Navigation } from 'swiper/modules';

function sliderRecommended() {
  new Swiper('.recommended__wrapper .swiper', {
    modules: [Navigation],
    spaceBetween: 13,
    grabCursor: true,
    slidesPerView: 2,
    navigation: {
      prevEl: '.swiper-button-prev',
      nextEl: '.swiper-button-next',
    },
    breakpoints: {
      1280: {
        slidesPerView: 2,
        spaceBetween: 25,
      },
      1440: {
        slidesPerView: 3,
        spaceBetween: 25,
      }
    }
  });
}
sliderRecommended();